.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: black;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .CreateUser {
    background-color: #9fa4c4;
    background-image: linear-gradient(315deg, #9fa4c4 0%, #9e768f 74%);
    min-height: 100vh;
    color: beige;
  }
  
  .ShowRoomDetails {
    background-color: #9fa4c4;
    background-image: linear-gradient(315deg, #9fa4c4 0%, #9e768f 74%);
    min-height: 100vh;
    color: beige;
  }
  
  .UpdateRoom {
    background-color: #9fa4c4;
    background-image: linear-gradient(315deg, #9fa4c4 0%, #9e768f 74%);
    min-height: 100vh;
    color: beige;
  }
  
  .ShowRoomList {
    background-color: #9fa4c4;
    background-image: linear-gradient(315deg, #9fa4c4 0%, #9e768f 74%);
    height: 100%;
    width: 100%;
    min-height: 100vh;
    min-width: 100px;
    color: black;
  }
  
  
  .list {
    display: grid;
    margin: 20px 0 50px 0;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: 1fr;
    grid-gap: 2em;
  }
  
  
  .card-container {
    width: 250px;
    border: 1px solid rgb(5, 5, 5);
    margin: 0 auto;
    border-radius: 5px;
    overflow: hidden;
  }
  /* .card-container image {
    width: 100%;
    height: 50%;
  } */
  
  .desc {
    height: 130px;
    padding: 10px;
  }
  
  .desc h2 {
    font-size: 1em;
    font-weight: 400;
  }
  
  
  .desc h3, p {
    font-weight: 300;
  }
  
  .desc h3  {
    color: #6c757d;
    font-size: 1em;
    padding: 10px 0 10px 0;
  }


  @import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&display=swap');


/*----------------------
   Header section
   -----------------------  */
* {
  margin: 0;
  padding: 0;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.navbar .container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;

  

}

.navbar {

  height: 60px;
  background-color: #E8E8E8;
  position: relative;

}


.logo {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  color: black;
}

.menu-icon {
  display: none;
  font-size: 30px;

}

ul {
  display: flex;
  justify-content: space-between;
  list-style-type: none;


}

ul li:not(:last-child) {
  margin-right: 60px;
}

ul a {
  font-size: 16px;
  font-weight: 400;
  color: #1B1A21;
  text-decoration: none;
}

ul a.active {
  color: #574c4c;
  font-weight: 500;
  position: relative;
}

ul a.active::after {
  /* content: ''; */
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #574c4c;

}

ul a:hover {
  color: #0b73f1;
  text-decoration: none;
}

@media (max-width:768px) {
  ul li:not(:last-child) {
    margin-right: 30px;
  }
}

@media (max-width: 600px) {
  .menu-icon {
    display: block;
    cursor: pointer;

  }

  .nav-element {

    position: absolute;
    right: 0;
    top: 60px;
    background-color: #99d6fb;
    width: 0px;
    height: calc(100vh - 60px);
    transition: all 0.3s ease-in;
    overflow: hidden;
    z-index: 2;
  }

  .nav-element.active {
    width: 270px;
  }

  .nav-element ul {
    display: flex;
    flex-direction: column;

  }

  .nav-element ul li {
    margin-right: unset;
    margin-top: 22px;
    text-align: center;
  }
}


